import React, { useState, useEffect } from "react"
import styled from "styled-components"
import posed from 'react-pose'
import _ from 'lodash'

import CTA from '../images/getintouchCTA.svg'

import {TweenLite, Power2, Power3} from "gsap/TweenMax";


import { connect } from 'react-redux';

const Container = styled.div`
    width: 10%;
    position: fixed;
    left: 0%;
    margin-left: -5%;
    bottom: 0;

    img {
        width: 100%;
        height: 100%
    }

`


const mapStateToProps = (state) => {
  return {
      isInfoPage: state.app.isInfoPage,
      isInViewport: state.app.isInViewport,
      isEnglish: state.app.isEnglish
  }
}

let ctaRotation = 0;


const CallToAction = () => {

    const [count, setCount] = useState(0);

    let initPageScrollVar = 0;


    useEffect(() => {

        const CTARef = document.querySelector('.CTA-image');
        const window = document.querySelector('.infinite-scroll');

        document.querySelector('.infinite-scroll').addEventListener('scroll', _.throttle(handleMenuScroll, 200));

      });

      const handleMenuScroll = () => {

        let initPageScroll = initPageScrollVar;
  
        if(window) {
  
          if(window.scrollTop >= initPageScroll) {

                TweenLite.to(CTARef, 3, {rotation: ctaRotation, x: ctaRotation, ease:Power3.easeOut});
                ctaRotation += 10;

                if(CTARef._gsTransform) {

                    if(CTARef._gsTransform.x > window.clientWidth) {
                        ctaRotation = 0;
                    }
                }
    
          } else if (window.scrollTop <= initPageScroll ) {

            TweenLite.to(CTARef, 3, {rotation: ctaRotation, x: ctaRotation, ease:Power3.easeOut});
            ctaRotation -= 10;
    
          }
    
          return initPageScrollVar = window.scrollTop;
  
        }
    }

    return (
        <Container className="CTA">
            <img className="CTA-image" src={CTA}/>
        </Container>
    )
  
}



  export default connect(mapStateToProps)(CallToAction);